import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutCmp } from '../cmps/layout/layout.cmp';
import { HomeCmp } from '../cmps/home.cmp';

import { LocationsCmp } from '../cmps/locations.cmp';

import { LeftComponent } from '../cmps/left/left.component';
import { TopComponent } from '../cmps/top/top.component';

export const routes: Routes = [
  {
    path: 'layout', component: LayoutCmp, children: [
      { path: 'home', component: HomeCmp },
     
      
      { path: 'locations', component: LocationsCmp },
      { path: 'left', component: LeftComponent, outlet: "left" },
      { path: 'top', component: TopComponent, outlet: "top" },
    ]
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TrinityRoutingModule { }



